
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import scroll from '@/components/scroll/scroll'
import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Tab,TabItem,Swiper, SwiperItem,InlineLoading,scroll,slide
},
data() {
return {
tabIndex: 0,
noData: [false, false, false],
start: "",
num: ["1", "12", "999"],
pageIndex: [1, 1, 1],
pageSize: 15,
list0: [],
list1: [],
list2:[],
}
},
mounted(){
this.getCurrentDate();
this.initData(true);
},
methods: {
slide(index){
},

getCurrentDate() {
var date = new Date();
var seperator = "";
var year = date.getFullYear();
var month = date.getMonth() + 1;
if (month >= 1 && month <= 9) {
month = "0" + month;
}
this.start = year + seperator + month;
},

initData(upOrDown) {
if (upOrDown) {
this.pageIndex[this.tabIndex] = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
'MSH.1': 'zkjl',
'MSH.2': 'list',
'CHZBM': this.$root.loginInfo.CHZBM,
'start': this.start,
'num': this.num[this.tabIndex],
'pageIndex': this.pageIndex[this.tabIndex],
'pageSize': this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
if (typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex[this.tabIndex] = 1;
switch (this.tabIndex) {
case 0:
this.list0 = [];
break;
case 1:
this.list1 = [];
break;
case 2:
this.list2 = [];
break;
default:
break;
}
}
if (res.rows.length > 0) {
this.noData[this.tabIndex] = false;
this.pageIndex[this.tabIndex]++;
switch (this.tabIndex) {
case 0:
this.list0 = [...this.list0,...res.rows];
break;
case 1:
this.list1 = [...this.list1,...res.rows];
break;
case 2:
this.list2 = [...this.list2,...res.rows];
break;
default:
break;
}
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData[this.tabIndex] = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
});
},
},
watch: {
tabIndex: function(newPage, oldPage) {
this.$refs.slide.goToPage(newPage);
switch (this.tabIndex) {
case 0:
if (this.list0.length <= 0) {
this.initData(true);
}
break;
case 1:
if (this.list1.length <= 0) {
this.initData(true);
}
break;
case 2:
if (this.list2.length <= 0) {
this.initData(true);
}
break;
default:
break;
}
}
},
}



